import React from 'react'
import { Card, Input, Button, Checkbox,Form, message } from 'antd';
import './index.scss'
import { history } from 'src/utils/history';
import { http, setToken } from 'src/utils';

export default function Login() {
  
  async function onFinish(value){
    
    const res = await http.post("admin/auth/login",{member:value.username,password:value.password})

    console.log("token",res)
    if(res.code !== 200){
      message.error("")
        return false;
    }
    setToken(res.data.token)
    if(res){
      message.success("")
      return history.push('/admin/index')        
    }
  }
    
  return (
    <>
      <div className="background" style={{ position: "absolute" }}>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div style={{ position: "absolute", width: "100vw" }}>
        <div style={{ width: "420px", margin: "30vh auto" }}>
          <Card bordered={false} title="" hoverable={true}>
            <Form onFinish={onFinish} initialValues={{ remember: true }}
            >
              <Form.Item name='username' lavel='username' rules={[{ required: true, 'message': "" }]}>
                <Input placeholder="" size='large' />
              </Form.Item>
              <div style={{ height: "10px" }}></div>

              <Form.Item name='password' lavel='password' rules={[{ required: true, 'message': "" }]}>
                <Input placeholder="" size='large' />
              </Form.Item>

              <div style={{ height: "20px" }}></div>


              <Form.Item name="remeber" valuePropName="ckecked">
                <Checkbox className='login-checkbox-label' style={{ color: "#1677ff" }}> []  []</Checkbox>
              </Form.Item>
              <div style={{ height: "10px" }}></div>


              <Form.Item>

                <Button size='large' htmlType='submit' type="primary" block>
                  
                </Button>
              </Form.Item>
            </Form>

          </Card>
        </div>
      </div>
    </>

  )
}
