import { Select, Table, Tabs, Input, Button, message, Popconfirm } from 'antd';
import TextArea from 'antd/es/input/TextArea';
import Modal from 'antd/es/modal/Modal';
import React, { useEffect, useState } from 'react'
import { http } from 'src/utils';

const { Option } = Select;

const items = [
  {
    key: 'public',
    label: ``,
  },
  {
    key: 'transfer',
    label: ``,
  },
  {
    key: 'action',
    label: ``,
  },
];

export default function Domain() {
  const columns = [
    {
      title: 'id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: '',
      dataIndex: 'domain',
      key: 'domain',
    },
    {
      title: '',
      dataIndex: 'status',
      key: 'status',
    },
    {
      title: '',
      dataIndex: 'we_chat_ban_status',
      key: 'we_chat_ban_status',
    },
    {
      title: 'ID',
      dataIndex: 'bind_service_id',
      key: 'bind_service_id',
    },
    {
      title: '',
      dataIndex: 'member',
      key: 'member',
    },
    {
      title: '',
      dataIndex: 'action',
      render: (text, record, index) => {
        console.log("res",index,text, record)
        return <>
          <Popconfirm
            onClick={() => {
              set_update_domain(record.domain)
            }}
            title=""
            description={<Input value={update_domain} onChange={(e) => { set_update_domain(e.target.value) }} />}
            onConfirm={() => {
              http.post("/admin/domain/update", { id: record.id, domain: update_domain, type: type, status: record.status }).then(() => {
                message.success('');
                set_update_domain("")
                initRes(type, username, is_bind_service, domain)
              })

            }}
            okText=""
            cancelText=""
          >
            <Button size="small"></Button>
          </Popconfirm>


            |
          <Popconfirm
            title=""
            description={"" + record.domain + "?"}
            onConfirm={() => {
              http.post("/admin/domain/delete", { domain_id: record.id }).then(() => {
                message.success('');
                initRes(type, username, is_bind_service, domain,page)
              })

            }}
            okText=""
            cancelText=""
          ><Button size="small"></Button>
          </Popconfirm>

        </>
      },
    }];

  const [page, set_page] = useState(1)
  const [type, set_type] = useState("public")
  const [domain, set_domain] = useState("")
  const [add_domain, set_add_domain] = useState("")
  const [data, set_data] = useState([])
  const [update_domain, set_update_domain] = useState([])


  const [resut,set_reult] = useState([])

  
  const [username, set_username] = useState("")

  const [is_bind_service, set_is_bind_service] = useState("0")

  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleOk = () => {
    http.post("/admin/domain/create", { domain: add_domain, type: type }).then(() => {
      set_add_domain("")
      message.success("")
      initRes(type, username, is_bind_service, domain,page)
      handleCancel();
    })
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };

  useEffect(() => {
    initRes("public", "", "", "",1)
  }, [])


  async function initRes(type, username, is_bind_service, domain,page) {
     http.post("/admin/domain/list", { page: page, offset: 15, type: type, is_bind_service: parseInt(is_bind_service), username: username, domain: domain }).then((e) => {
      set_data(e.data.list)
      set_reult(e.data)
    })
  }

  const onTabChange = (e) => {
    set_type(e)
    set_page(1)
    initRes(e, username, is_bind_service, domain,1)
  }


  return (
    <div>
      <Modal title="" cancelText="" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
        <p></p>
        <TextArea placeholder=",http://" value={add_domain} onChange={(e) => { set_add_domain(e.target.value) }} style={{ height: "200px" }} />
      </Modal>

      <Tabs defaultActiveKey={type} items={items} onChange={onTabChange} />
      <span>

        <Button type="primary" htmlType="submit" style={{ width: 80, margin: '0 8px 0 0' }} onClick={() => {
          setIsModalOpen(!isModalOpen)
        }} >
          
        </Button>

        <Select
          value={is_bind_service}
          style={{ width: 100, margin: '0 8px' }}
          onChange={(e) => { set_is_bind_service(e) }}
        >
          <Option value="0"></Option>
          <Option value="1"></Option>
          <Option value="2"></Option>
        </Select>


        <Input
          type="text"
          placeholder=""
          value={username}
          onChange={(e) => { set_username(e.target.value) }}
          style={{ width: 180, margin: '0 8px' }}
        />


        <Input
          type="text"
          placeholder=""
          value={domain}
          onChange={(e) => { set_domain(e.target.value) }}
          style={{ width: 180, margin: '0 8px' }}
        />

        <Button type="primary" htmlType="submit" onClick={() => {
          set_page(1)
          initRes(type, username, is_bind_service, domain,page)
        }}>
          
        </Button>
      </span>
      <div style={{ height: "15px" }}></div>

      <Table size="small" onChange={(e)=>{
        set_page(e.current)
        initRes(type, username, is_bind_service, domain,e.current)
      }} columns={columns} pagination={{current:page,total:resut.count,pageSize:15,position:["bottomCenter"]}} dataSource={data} />
    </div>
  )
}
