import './App.css';
import { unstable_HistoryRouter as HistoryRouter, Routes, Route, Navigate } from 'react-router-dom';
import { history } from './utils/history';
import Home from '@/pages/Index';
import Login from '@/pages/Login';
import { AuthComponent } from './components/AuthComponent';


function App() {
  return (
    <HistoryRouter history={history}>
      <div className="App">
        <Routes>
          <Route path='/admin/login' element={<Login />}> </Route>
          <Route path='/admin' element={<AuthComponent>{<Navigate to="/admin/index" replace />}</AuthComponent>}> </Route>
          <Route path='/' element={<AuthComponent>{<Navigate to="/admin/index" replace />}</AuthComponent>}> </Route>
          <Route path='/admin/index' element={<AuthComponent><Home /></AuthComponent>}></Route>
        </Routes>
      </div>
    </HistoryRouter>

  );
}

export default App;
