import { Select, Table, Tabs, Input, Button, message, Popconfirm, Radio } from 'antd';
import Modal from 'antd/es/modal/Modal';
import React, { useEffect, useState } from 'react'
import { http } from 'src/utils';
import Orders from './Orders';
const { Option } = Select;

const items = [
  {
    key: 'group',
    label: ``,
  },
];

export default function Agent() {
  const columns = [
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: '',
      dataIndex: 'username',
      key: 'username',
    },
    {
      title: '',
      dataIndex: 'password',
      key: 'password',
    },
    {
      title: '',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: '',
      dataIndex: 'agent_price',
      key: 'agent_price',
    },
    {
        title: 'Usdt',
        dataIndex: 'account',
        key: 'account',
      },
    {
      title: '',
      dataIndex: 'create_time',
      key: 'create_time',
    },
    {
      title: '',
      dataIndex: 'action',
      render: (text, record, index) => {
        console.log("res", index, text, record)
        return <>
          <Popconfirm
            placement="left"
            title={<span><b style={{color:"red"}}> {record.member} </b></span>}
            description={<>
            <div><Input prefix="Usdt" value={updateAccount} onChange={(e) => { set_updateAccount(e.target.value) }} /></div> </>}
            onConfirm={() => {
              console.log( { code:record.code,account:parseInt(updateAccount)})
              http.post("/admin/agent/add_account", { code:record.code,account:parseInt(updateAccount)}).then((e) => {
                if (e.code !== 200){
                    message.error(e.msg)
                }else{
                  message.success('');
                  set_account(0)
                  initRes("","", page)
                }
              })

            }}
            okText=""
            cancelText=""
          >
            <Button size="small"></Button>
          </Popconfirm>

          <span style={{width:"40px"}}> | </span> 

          <Modal title="" 
            open={isOrderModalOpen} 
            onOk={()=>{setIsOrderModalOpen(false)}} 
            onCancel={()=>{setIsOrderModalOpen(false)}}>
            <Orders></Orders>
        </Modal>
        <Button size="small" onClick={()=>{
          setIsOrderModalOpen(true)
          }}></Button>
        </>
      },
    }];


  // page
  const [page, set_page] = useState(1)
  const [data, set_data] = useState([])

  // create
  const [username,set_username] = useState("")
  const [password,set_password] = useState("")
  const [price, set_price] = useState(50)

  // renew 
  const [updateAccount,set_updateAccount] = useState("")

  // agent 
  const [code, set_code] = useState("")

  // renew    
  const [account, set_account] = useState(0)

  // list
  const [resut, set_reult] = useState([])

  // model
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isOrderModalOpen, setIsOrderModalOpen] = useState(false);
  const [isShowCreateMember,setisShowCreateMember] = useState(false)  

  const handleOk = () => {
    message.success("....")

    http.post("/admin/agent/create", { username:username,password:password,agent_price:price}).then((e) => {
      set_price(50)
      set_code(e.data.code)
      message.success("")
      initRes("", "", page)
      setisShowCreateMember(true)
    })
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };

  useEffect(() => {
    initRes("user", "", 1)
  }, [])


  async function initRes(type, username, page) {
    http.post("/admin/agent/list", { page: parseInt(page),limit:15}).then((e) => {
      console.log("res",e.data)
      set_data(e.data.data)
      set_reult(e.data)
    })
  }


  return (
    <div>
       <Modal title="" open={isShowCreateMember} onOk={()=>{setisShowCreateMember(false)}} onCancel={()=>{setisShowCreateMember(false)}}>
        <p>http://www.xio.ink/agent</p>
        <p>: {code}</p>
           
        <p></p>
      </Modal>

      <Modal title="" cancelText="" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
        <p></p>
        <div style={{ height: "15px" }}></div>
        <div><Input prefix="" placeholder="" value={username} onChange={(e) => { set_username(e.target.value) }} /></div>
        <div style={{ height: "15px" }}></div>
        <div><Input prefix="" placeholder=" " value={password} onChange={(e) => { set_password(e.target.value) }} /></div>
        <div style={{ height: "15px" }}></div>
        <div><Input prefix="" placeholder="50" type="number" value={price} onChange={(e) => { set_price(e.target.value) }} /></div>
      </Modal>

      <Tabs />
      <span>

        <Button type="primary" htmlType="submit" style={{ width: 80, margin: '0 8px 0 0' }} onClick={() => {
          setIsModalOpen(!isModalOpen)
        }} >
          
        </Button>

      </span>
      <div style={{ height: "15px" }}></div>

      <Table size="small" onChange={(e) => {
        console.log("search page",e)
        set_page(e.current)
        initRes("","", e.current)
      }} columns={columns} pagination={{ current: page, total: resut.count, pageSize: 15, position: ["bottomCenter"] }} dataSource={data} />
    </div>
  )
}
