import React, { useState } from 'react'
import {
  AppstoreOutlined,
  LoginOutlined,
  DesktopOutlined,
  MailOutlined,
} from '@ant-design/icons';
import { Menu } from 'antd';
import Layout, { Content } from 'antd/es/layout/layout';
import Sider from 'antd/es/layout/Sider';
import Config from '../System/Config';
import Domain from '../System/Domain';
import Service from '../App/Service';
import Orders from '../App/Orders';
import Users from '../App/Users';
import Homes from '../App/Homes';
import Agent from '../App/Agent';

import { removeToken } from 'src/utils';
import { history } from 'src/utils/history';


function getItem(label, key, icon, children, type) {
  return {
    key,
    icon,
    children,
    label,
    type,
  };
}
const items = [
  getItem('', 'home', <DesktopOutlined />),
  getItem('', 'domain', <DesktopOutlined />),
  getItem('', 'service', <DesktopOutlined />),
  getItem('', 'agent', <DesktopOutlined />),


  // getItem('', 'system', <MailOutlined />, [
  //   getItem('', 'config'),
  //   getItem('', 'domain'),
  // ]),
  // getItem('', 'app', <AppstoreOutlined />, [
  //   getItem('', 'service'),
  //   getItem('', 'users'),
  //   getItem('', 'order'),
  // ]),
  getItem('', 'out', <LoginOutlined />),

];

const TabMap = {
  "home": <Homes/>,
  "config": <Config />,
  "domain": <Domain />,
  "agent": <Agent />,
  "service": <Service />,
  "users":<Users/>,
  "order" :<Orders/>,
}

const NameMap = {
  "home": "",
  "config": "",
  "domain": "",
  "agent": "",
  "service": "",
  "users":"",
  "order" :"",
}

export default function Home() {

  const [action_status,set_action_status] = useState("home")

  const action = (e) => {
    console.log("e",e)
    if (e.key === "out"){
      removeToken()
      history.push('/admin/login')
      return
    }
    set_action_status(e.key)
  }

  return (
    <>
      <Layout>
        <Sider>
          <div style={{ height: "100vh" }} >
            <Menu
              defaultSelectedKeys={['1']}
              defaultOpenKeys={['sub1']}
              mode="inline"
              theme="dark"
              items={items}
              onSelect = {(e)=>action(e)}
            />
          </div>

        </Sider>
        <Content style={{ height: "100vh"}}  >
            <div style={{display:"flex",flexDirection:"column",height:"100vh",overflowY:"scroll"}}>
              <div style={{padding:"15px",background:"#fff",margin:"15px 25px 15px 25px"}}>
              {NameMap[action_status]}
              </div>
      
              <div style={{padding:"15px",background:"#fff",margin:"0px 25px 25px 25px",flex:1}}>
              {TabMap[action_status]}
              </div>
            </div>
        </Content>

      </Layout>

    </>
  )
}
